import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import axios from 'axios'
import zconst from './components/store/const'
import masterData from './components/store/masterData'
//echarts
import echarts from 'echarts'

// import 'font-awesome/scss/font-awesome.scss'

Vue.use(ElementUI)
// Vue.use(VueAMap)
// axiosURLapi
// var axios = require('axios')
// axios.defaults.baseURL = 'http://localhost:8443/api'
// API
axios.defaults.baseURL = process.env.API
Vue.prototype.$axios = axios
Vue.prototype.BASE_URL = zconst.BASE_URL
Vue.prototype.MT_DATA = masterData

Vue.prototype.$echarts = echarts //$echartsVue
Vue.config.productionTip = false

/* eslint-disable */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

// https://www.cnblogs.com/wbjxxzx/p/9942867.html
//  /login
//  vue-router  beforeEach 
// beforeEach 
// to: Route:  
// from: Route: 
// next: Function:  resolve  next 
router.beforeEach((to, from, next) => {
  if(to.path === '/login'){
    sessionStorage.removeItem('user');
  }
  var user = sessionStorage.getItem('user');
  if(!user && to.path !== '/login'){
    next({
      path: '/login'
    })
  }else{
    next();
  }
})


