<template>
  <div class="app">
    <el-container style="height: 100%; border: 1px #eee">
      <el-header class="app-header">
        <el-row>
          <el-col :span="6">
            <div align="center">
              <img src="@/assets/logo.png" :width="isCollapse ? '60' : '60'" height="60">
            </div>
          </el-col>
          <el-col :span="18">
            <!--  -->
            <el-menu
              :default-active="defaultActive"
              class="el-menu-demo"
              mode="horizontal"
              @select="handleSelect"
              background-color="#545c64"
              text-color="#fff"
              active-text-color="#ffd04b"
            >
              <el-menu-item index="/"></el-menu-item>
              <el-menu-item @click.native="back"></el-menu-item>
              <el-submenu index="/2">
                <template slot="title"></template>
                <el-menu-item index="2-1"></el-menu-item>
                <el-menu-item index="2-2"></el-menu-item>
                <el-menu-item index="2-3" @click.native="logout"></el-menu-item>
              </el-submenu>

            </el-menu>
          </el-col>
        </el-row>
      </el-header>
      <el-container>
        <el-aside class="app-side app-side-left" :class="isCollapse=false">
          <div>
            <!--  -->
            <el-menu
              :default-active="defaultActive"
              router
              class="el-menu-vertical-demo"
              @open="handleOpen"
              :collapse="isCollapse"
              background-color="#545c64"
              text-color="#fff"
              active-text-color="#ffd04b"
              @select="handleSelect"
            >
              <el-submenu index="1">
                <template slot="title">
                  <i class="el-icon-setting"></i>
                  <span></span>
                </template>
                <el-menu-item-group>
                  <el-menu-item index="/assetType"></el-menu-item>
                  <el-menu-item index="/asset"></el-menu-item>
                  <el-menu-item index="/location">/</el-menu-item>
                  <el-menu-item index="/tourType"></el-menu-item>
                  <el-menu-item index="/resvType"></el-menu-item>
                  <el-menu-item index="/event"></el-menu-item>
                  <el-menu-item index="/customer"></el-menu-item>
                  <el-menu-item index="/driver"></el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-submenu index="2">
                <template slot="title">
                  <i class="el-icon-s-order"></i>
                  <span></span>
                </template>
                <el-menu-item-group>
                  <el-menu-item index="/createSch"></el-menu-item>
                  <el-menu-item index="/schedule"></el-menu-item>
                  <el-menu-item index="/reservation"></el-menu-item>
                  <el-menu-item index="/createtour"></el-menu-item>
                  <el-menu-item index="/tourList"></el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-submenu index="3">
                <template slot="title">
                  <i class="el-icon-s-promotion"></i>
                  <span></span>
                </template>
                <el-menu-item-group>
                  <!-- <template slot="title"></template> -->
                  <el-menu-item index="/monitor"></el-menu-item>
                  <el-menu-item index="/searchTour"></el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-submenu index="4">
                <template slot="title">
                  <i class="el-icon-s-marketing"></i>
                  <span></span>
                </template>
                <el-menu-item-group>
                  <!-- <template slot="title"></template> -->
                  <el-menu-item index="/cost"></el-menu-item>
                  <el-menu-item index="/tourRep"></el-menu-item>
                  <el-menu-item index="/tourUsage"></el-menu-item>
                  <el-menu-item index="/tourETA"></el-menu-item>
                  <el-menu-item index="/tourbroken"></el-menu-item>
                  <el-menu-item index="/tourHistory"></el-menu-item>
                </el-menu-item-group>
              </el-submenu>
            </el-menu>
          </div>
        </el-aside>
        <el-main class="app-body">
          <template>
            <router-view/>
          </template>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>


<script>
export default {
  name: "Container",
  data() {
    return {
      username: "",
      isCollapse: false
    };
  },
  created() {
    //get all locations
    this.$axios
      .get("/api/v1/loc", {
        params: {
          locId: null,
          address: null
        }
      })
      .then(response => {
        debugger;
        this.MT_DATA.locations = response.data;
      })
      .catch(function(error) {
        alert(error);
      });

    //get all vehicles
    this.$axios
      .get("/api/v1/asset", {
        params: {
          assetId: null,
          assetTpye: null
        }
      })
      .then(response => {
        debugger;
        this.MT_DATA.assets = response.data;
      })
      .catch(function(error) {
        alert(error);
      });

    //get all tour types
    this.$axios
      .get("/api/v1/tourType", {
        params: {
          tourType: null
        }
      })
      .then(response => {
        debugger;
        this.MT_DATA.tourTypes = response.data;
      })
      .catch(function(error) {
        alert(error);
      });

    //get all customrs
    this.$axios
      .get("/api/v1/customer", {
        params: {
          custId: null
        }
      })
      .then(response => {
        debugger;
        this.MT_DATA.customers = response.data;
      })
      .catch(function(error) {
        alert(error);
      });

    //get all drivers
    this.$axios
      .get("/api/v1/driver", {
        params: {
          custId: null
        }
      })
      .then(response => {
        debugger;
        this.MT_DATA.drivers = response.data;
      })
      .catch(function(error) {
        alert(error);
      });

    //get reservation type
    this.$axios
      .get("/api/v1/resvType",{
        params: {
            resvType: null
          }
      })
      .then(response => {
        debugger
        this.MT_DATA.resvTypes = response.data;
      })
      .catch(function(error) {
        alert(error);
      });

    //get schedule type
    this.$axios
      .get("/api/v1/schType",{
        params: {
            resvType: null
          }
      })
      .then(response => {
        debugger
        this.MT_DATA.schTypes = response.data;
      })
      .catch(function(error) {
        alert(error);
      });
  },
  computed: {
    // , 
    defaultActive: function() {
      return this.$route.path;
    }
  },
  methods: {
    toggleSideBar() {
      this.isCollapse = !this.isCollapse;
    },
    logout: function() {
      this.$confirm("?", "", {})
        .then(() => {
          sessionStorage.removeItem("user");
          this.$router.push("/login");
        })
        .catch(() => {});
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    back(){
      this.$router.go(-1);//
    }
  },
  mounted: function() {
    let user = sessionStorage.getItem("user");
    if (user) {
      this.username = user;
    }
  }
};
</script>



